<template>
    <header class="header">
        <img :src="photo[src]" alt="" class="header__photo">
        <div class="header__block">
            <h3 class="header__block-name">{{ name }}</h3>
            <span class="header__block-status">{{ status }}</span>
        </div>
        <div class="header__clear" @click="this.$emit('delLocalStorage')">
            <img src="@/assets/img/clear.png" alt="">
        </div>
    </header>
</template>

<script>
    export default {
        props: ['name', 'src', 'status'],
        data() {
            return {
                photo: {
                    alex: require('@/assets/img/alex.png'),
                    jane: require('@/assets/img/jane.png')
                }
            }
        },
    }
</script>

