<template>
  <div class="wrapper" oncontextmenu="return false">
    <div class="chat">
      <Header :src="'alex'" :name="'Александр'" :status="'Онлайн'" @delLocalStorage="delLocalStorage"/>
      <Main :smsStorage="smsStorage" :id="1" @delMes="delMes" @saveChange="saveChange" />
      <Footer :id="1" @sendMess="sendMess" :a="a"/>
    </div>

    <div class="chat">
      <Header :src="'jane'" :name="'Евгений'" :status="'Онлайн'" @delLocalStorage="delLocalStorage"/>
      <Main :smsStorage="smsStorage" :id="2" @delMes="delMes" @saveChange="saveChange" />
      <Footer :id="2" @sendMess="sendMess" :a="a"/>
    </div>
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import Main from "@/components/Main.vue";
import Footer from "@/components/Footer.vue";
export default {
  components:{
    Header,
    Main,
    Footer
  },
  data(){
    return {
      smsStorage: [],
      a: null
    }
  },
  mounted(){
    const messageStorage = localStorage.message
    this.smsStorage = messageStorage ? JSON.parse(messageStorage) : []
  },
  methods:{
    sendMess(sms){
      this.smsStorage.push(sms)
      localStorage.message = JSON.stringify(this.smsStorage)
    },
    delLocalStorage(){
      this.smsStorage.splice(0, Infinity)
      localStorage.clear()
    },
    delMes(i){
      let myArray = JSON.parse(localStorage.getItem('message')) || []
      let ind = myArray.findIndex(item => item.id = i)
      myArray.splice(ind, 1)
      localStorage.setItem('message', JSON.stringify(myArray))
      
      this.smsStorage.splice(ind, 1);
    },
    saveChange(Change, i, sms){
      let cur = this.smsStorage.findIndex(el => el.id == i)
      this.a = cur
      
      this.smsStorage.forEach(sms => {
        if (sms.id == Change.i ) {
          sms.sms = Change.text
        }
      })
    }
  }
};
</script>
