<template>
  <footer class="footer">
    <textarea
      rows="1"
      class="footer__textarea"
      placeholder="Написать сообщение..."
      v-model="message"
      @input="areaText"
    />
    <button class="footer__photo" v-if="!message" @click="closeModal = true">
      <img src="@/assets/img/photo.svg" alt="" />
    </button>
    <button class="footer__photo" v-else @click="sendMess">
      <img src="@/assets/img/send.svg" alt="" />
    </button>

      <Transition name="bounce">
    <div class="modal" @click="closeModal = false" v-if="closeModal">
      <div class="modal__block" @click.stop="">
        <h2 class="modal__block-title">Отправить картинку</h2>
        <div class="modal__block-inputs">
          <label for="inp">
            <span>URL</span>
            <input type="text" id="inp" v-model="url" />
          </label>
          <label for="area">
            <span>comment</span>
            <textarea id="area" v-model="comment"></textarea>
          </label>
        </div>
        <div class="modal__block-btns">
          <button class="modal__block-btns-btn del" @click="closeModal = false">
            Отмена
          </button>
          <button class="modal__block-btns-btn edit" @click="sendPhoto">
            Добавить
          </button>
        </div>
      </div>
    </div>
  </Transition>
  </footer>


</template>

<script>
import { v4 as uuidv4 } from "uuid";
export default {
  props: ["id", "a"],
  data() {
    return {
      message: "",
      closeModal: false,
      url: "",
      comment: "",
    };
  },
  methods: {
    areaText() {
      const area = document.querySelectorAll(".footer__textarea");

      area.forEach((el) => {
        el.style.height = `auto`;
        el.style.height = `${el.scrollHeight}px`;
      });
    },
    sendMess() {
      const mins = new Date().getMinutes();
      let times = mins >= 10 ? mins : `0${mins}`;
      if (this.message.trim().length) {
        const mess = {
          id: this.id,
          i: uuidv4(),
          a: this.a,
          sms: this.message,
          time: `${new Date().getHours()}:${times}`,
        };
        this.$emit("sendMess", mess);
        this.message = "";

        const area = document.querySelectorAll(".footer__textarea");
        area.forEach((el) => {
          el.style.height = `auto`;
        });
      }
    },
    sendPhoto() {
      const mins = new Date().getMinutes();
      let times = mins >= 10 ? mins : `0${mins}`;
      if (this.url.trim().length && this.comment.trim().length) {
        const mess = {
          id: this.id,
          i: uuidv4(),
          a: this.a,
          sms: this.comment,
          url: this.url,
          time: `${new Date().getHours()}:${times}`,
        };
        this.$emit("sendMess", mess);
        this.url = this.comment = '';
        this.closeModal = false
      }
    },
  },
};
</script>

<style lang="scss" scoped>
</style>