<template>
  <main class="main" @click="openChange = false">
    <div class="main__block" 
    v-for="(sms, index) in smsStorage" 
    :key="index"
    :class="{
        edit: sms.id == id,
        send: sms.id !== id
    }">
      <div class="main__block-item">
        <div class="main__block-item-content" @click.right="openChange = true">
          <img :src="sms.url" alt="" v-if="sms.url">
          <p>
            {{ sms.sms }}
          </p>
        </div>
        <span>{{ sms.time }}</span>
        
        <div class="main__block-change" v-if="openChange" @click="openChange = false">
          <button class="main__block-change-chen" @click="openCreate = true">change</button>
          <button class="main__block-change-del" @click="this.$emit('delMes', sms.i)">dellate</button>
        </div>
      </div>
    </div>
    
    <div class="modalChange" v-if="openCreate">
      <span>Change</span>
      <input type="text" v-model="text">
      <div class="modalChange__btns">
        <button @click="openCreate = false">bask</button>
        <button @click="saveChange(sms.i, sms)">ok</button>
      </div>
    </div>
  </main>
  
</template>

<script>
export default {
  props: ["smsStorage", "id"],
  data() {
    return {
      openChange: false,
      openCreate: false,
      text: ''
    }
  },
  methods: {
    saveChange(i, sms){
      if (text != '') {
        const change = {
          text: this.text
        }
        this.$emit('saveChange', change, i, sms)
      }
    }
  },
};
</script>                          
                           